html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Kumbh Sans", sans-serif;
  background-color: var(--dark-cyan);
  color: var(--dark-blue-desaturated);
}

main {
  position: relative;
}

:root {
  --dark-cyan: hsl(185, 75%, 39%);
  --dark-cyan-transparent: hsla(185, 75%, 39%, 0.257);
  --dark-blue-desaturated: hsl(229, 23%, 23%);
  --dark-grayish-blue: hsl(227, 10%, 46%);
  --dark-gray: hsl(0, 0%, 59%);
  --light-gray: hsl(0, 0%, 91%);
  --white: hsl(0, 0%, 100%);
}

h3 {
  font-size: 0.9375rem;
  margin-bottom: 0.9375rem;
}

.grid {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  padding: 0 1.5rem;
  margin: auto;
}
@media (min-width: 71.875em) {
  .container {
    max-width: 69.375rem;
    padding: 0;
  }
}

.h-full {
  min-height: 100vh;
}

.card {
  width: 20.375rem;
  background-color: var(--white);
  border-radius: 0.875rem;
  overflow: hidden;
  box-shadow: 0 0.625rem 2.1875rem 0.625rem var(--dark-cyan-transparent);
}
.card__bg {
  padding: 4.6875rem;
  background-size: cover;
  z-index: -1;
}
.card__body {
  padding: 1.5625rem;
  position: relative;
  width: 100%;
  top: -4.6rem;
  margin-bottom: -4.6rem;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-bottom: 0.0625rem solid var(--light-gray);
}
.card__image {
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  overflow: hidden;
  border: 0.375rem solid var(--white);
  margin-bottom: 0.9375rem;
}
.card__image img {
  width: 100%;
  object-fit: cover;
}
.card__username--light {
  font-weight: 400;
  font-size: 0.875rem;
  color: var(--dark-grayish-blue);
}
.card__location {
  font-size: 0.726875rem;
  color: var(--dark-grayish-blue);
}
.card__footer {
  padding: 1.5625rem 2.625rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}
.card__count--value {
  font-weight: 700;
  font-size: 0.9375rem;
}
.card__count--desc {
  color: var(--dark-gray);
  font-size: 0.625rem;
  letter-spacing: 0.0625rem;
}

.pattern img {
  max-width: 100%;
}
.pattern--top {
  position: absolute;
  z-index: -1;
  pointer-events: none;
  top: 0;
  left: 0;
}
@media (min-width: 71.875em) {
  .pattern--top {
    top: -462px;
    left: -122px;
  }
}
.pattern--bottom {
  display: none;
  position: absolute;
  z-index: -1;
  pointer-events: none;
  top: 0;
  left: 0;
}
@media (min-width: 71.875em) {
  .pattern--bottom {
    bottom: -462px;
    right: -122px;
  }
}